@import "./components/header";
@import "zeroing";
@import "important";
@import "font-face";
@import "mixins";

.container {
    width: 1170px;
    margin: 0 auto;
}
 .header .burger__menu._active .burger__container .burger__navigation .link__one {
    color: #FEAB47;
 }
 .header .header__section .header__navigation .link__one {
    color: #FEAB47;
 }
 .tablet {
     display: none;
 }
 .phone {
     display: none;
 }
 .phone-img {
     display: none;
 }

.section:nth-child(1) {
    position: relative;
    .img {
        width: 100%;
        max-height: 820px;
    }
    .container {
        top: 0px;
        left: 0px;
        right: 0px;
        position: absolute;
        .block__h1 {
            position: absolute;
            top: 154px;
            right: 0px;
            left: 0px;
            margin: 0 auto;
            width: 510px;
            text-align: center;
            h1 {
                font-family: 'Nunito';
                font-style: normal;
                font-weight: 600;
                font-size: 56px;
                line-height: 135.4%;
                text-align: center;
                color: #FFFFFF;
                width: 510px;
            }
            p {
                padding: 30px 0px 45px 0px;
                width: 518px;
                font-family: 'Nunito';
                font-style: normal;
                font-weight: 600;
                font-size: 15px;
                line-height: 20px;
                text-align: center;
                color: #FFFFFF;
            }
            .btn {
                text-align: center;
            }
        }
    }
}
.section:nth-child(2) {
    h2 {
        text-align: center;
        padding-bottom: 60px;
    }
    .block {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    .block__left {
        display: flex;
        align-items: center;
        padding-right: 120px;
        .block__bottom {
            border: 6px solid #000000;
            height: 350px;
            width: 0px;
            border-radius: 10px;
            position: relative;
            .bottom__circle {
                width: 30px;
                height: 30px;
                background: #FFFFFF;
                border: 1px solid #FEA746;
                border-radius: 23px;
                position: absolute;
                left: -15px;
                top: 0px;
                cursor: pointer;
            }
            .bottom__circle:nth-child(1) {
                top: 300px;
            }
            .bottom__circle:nth-child(2) {
                top: 230px;
            }
            .bottom__circle:nth-child(3) {
                top: 166px;
            }
            .bottom__circle:nth-child(4) {
                top: 96px;
            }
            .bottom__circle:nth-child(5) {
                top: 34px;
            }
            .bottom__circle._active {
                background: #FEA746;
            }
        }
        .block__year {
            display: flex;
            flex-direction: column;
            padding-top: 30px;
            padding-left: 40px;
            span {
                padding: 17px 0px;
                font-family: 'Nunito';
                font-style: normal;
                font-weight: 600;
                font-size: 24px;
                line-height: 33px;
                text-align: center;
                color: #000000;
                width: 103px;
            }
        }
    }
    .block__right {
        display: flex;
        flex-direction: column;
        align-items: start;
    }
    .block__title {
        display: none;
    }
    .block__title._active {
        display: flex;
        flex-direction: column;
        align-items: start;
        span {
            font-family: 'Nunito';
            font-style: normal;
            font-weight: 600;
            font-size: 15px;
            line-height: 20px;
            color: #000000;
            width: 100%;
            padding-bottom: 35px;
        }
        .block__historie {
            display: flex;
            align-items: center;
            justify-content: space-between;
            .block__text {
                padding-left: 30px;
                h3 {
                    font-family: 'Nunito';
                    font-style: normal;
                    font-weight: 700;
                    font-size: 40px;
                    line-height: 55px;
                    color: #000000;
                }
                p {
                    padding-top: 15px;
                    font-family: 'Nunito';
                    font-style: normal;
                    font-weight: 600;
                    font-size: 15px;
                    line-height: 20px;
                    color: #000000;
                    width: 100%;
                }
            }
        }
        .block__historie-three {
            display: flex;
            flex-direction: column;
            align-items: start;
            span {
                padding-top: 40px;
            }

            .span-two {
                text-align: right;
            }

            .block__historie {
                display: flex;
                align-items: center;
                justify-content: space-between;
                .block__text {
                    padding-left: 30px;
                    h3 {
                        font-family: 'Nunito';
                        font-style: normal;
                        font-weight: 700;
                        font-size: 40px;
                        line-height: 55px;
                        color: #000000;
                    }
                    p {
                        padding-top: 15px;
                        font-family: 'Nunito';
                        font-style: normal;
                        font-weight: 600;
                        font-size: 15px;
                        line-height: 20px;
                        color: #000000;
                        width: 100%;
                    }
                }
            }
            .block__historie-two {
                flex-direction: row-reverse;
                .block__text {
                    padding-left: 0px;
                    padding-right: 30px;
                }
            }
        }
        .block__historie-four {
            display: flex;
            flex-direction: column;
            align-items: start;
            span {
                padding-top: 40px;
            }

            .span-two {
                text-align: right;
            }

            .block__historie {
                display: flex;
                align-items: center;
                justify-content: space-between;
                .block__text {
                    padding-left: 30px;
                    h3 {
                        font-family: 'Nunito';
                        font-style: normal;
                        font-weight: 700;
                        font-size: 40px;
                        line-height: 55px;
                        color: #000000;
                    }
                    p {
                        padding-top: 15px;
                        font-family: 'Nunito';
                        font-style: normal;
                        font-weight: 600;
                        font-size: 15px;
                        line-height: 20px;
                        color: #000000;
                        width: 100%;
                    }
                }
            }
            .block__historie-two {
                flex-direction: row-reverse;
                .block__text {
                    padding-left: 0px;
                    padding-right: 30px;
                }
            }
        }
    }
    
}
.section:nth-child(3) {
    h2 {
        text-align: center;
    }
    .slider {
        padding-top: 60px;
    }
    .arrow__slider {
        width: 1170px;
        position: relative;
        bottom: 160px;
        .prev {
            position: absolute;
            left: 0px;
        }
        .next {
            position: absolute;
            right: 0px
        }
    }
}

.footer {
    margin-top: 120px;
}

@import "./components/footer";
@import "breakpoints/breakpoint-about";

.footer .footer__container .footer__section .footer__navigation .link__one {
    color: #FEAB47;
}
.footer .footer__container-phone .footer__section .footer__section-left .footer__navigation .link__one {
    color: #FEAB47;
}