        font-size: 30px;
        padding-bottom: 40px;
    }
    .section {
        width: 100%;
        padding-top: 60px;
    }
    .section:nth-child(2) .block__left {
        padding-right: 40px;
    }
    .section:nth-child(2) .block__left .block__year span {
        font-size: 18px;
    }
