    }
    .section:nth-child(2) h2 {
        text-align: center;
        padding-bottom: 30px;
    }
}
@media screen and(max-width: 800px) {
    .container {
        width: 540px;
    }
    .section:nth-child(1) .container .block__h1 h1 {
        font-size: 34px;
    }
    .section:nth-child(1) .container .block__h1 p {
        padding: 15px 0px 30px 0px;
        width: 100%;
        font-size: 14px;
        font-weight: 400;
    }
    .section {
        padding-top: 40px;
    }
    .section:nth-child(2) .block__left {
        padding-right: 20px;
    }
    .section:nth-child(2) .block__title._active span {
        font-size: 14px;
        padding-bottom: 20px;
    }
    .section:nth-child(2) .block__title._active .block__historie .block__text p {
        font-size: 12px;
        line-height: 17px;
    }
    .section:nth-child(2) .block__title._active .block__historie .block__text h3 {
        font-size: 22px;
        line-height: 26px;
    }
    .decstop {
        display: none;
    }
    .tablet {
        display: block;
    }
    .section:nth-child(2) .block__title._active .block__historie .tablet {
        width: 206px;
    }
    .section:nth-child(2) .block__left .block__year span {
        font-size: 16px;
    }
    .section:nth-child(2) .block__left .block__bottom {
        border: 4px solid #000000;
        height: 280px;
    }
    .section:nth-child(2) .block__left .block__year span {
        padding: 13px 0px;
        line-height: 23px;
        width: 83px;
    }
    .section:nth-child(2) .block__left .block__bottom .bottom__circle {
        width: 20px;
        height: 20px;
        left: -10px;
    }
    .section:nth-child(2) .block__left .block__bottom .bottom__circle:nth-child(4) {
        top: 86px;
    }
    .section:nth-child(2) .block__left .block__bottom .bottom__circle:nth-child(3) {
        top: 136px;
    }
    .section:nth-child(2) .block__left .block__bottom .bottom__circle:nth-child(2) {
        top: 184px;
    }
    .section:nth-child(2) .block__left .block__bottom .bottom__circle:nth-child(1) {
    top: 234px;
    }

    .section:nth-child(1) .img {
        width: 100%;
        max-height: 600px;
    }
    .section:nth-child(1) .container .block__h1 {
        top: 24px;
        width: 450px;
    }
}
@media screen and(max-width: 576px) {
    .container {
        width: 85%;
    }
    .slider__image {
        img {
            width: 100%;
        }
    }
    h2 {
        font-size: 22px;
        padding-bottom: 0px;
    }
    .section:nth-child(3) .slider {
        padding-top: 20px;
    }
    .section:nth-child(1) .container .block__h1 {
        top: 24px;
        width: 100%;
    }
    .section:nth-child(1) .container .block__h1 h1 {
        font-size: 26px;
        width: 100%;
    }
    .section:nth-child(1) .container .block__h1 p {
        padding: 10px 0px 25px 0px;
        width: 70%;
        font-size: 13px;
        font-weight: 400;
        margin: 0 auto;
    }
    .section:nth-child(1) .img {
       display: none;
    }
    .phone-img {
        display: block;
        width: 100%;
        max-height: 600px;
        height: 310px;
    }
    .phone {
        display: none;
    }
    .tablet {
        display: none;
    }
    .decstop {
        display: block;
    }
    .section:nth-child(2) .block__title._active .block__historie img {
        width: 244px;
    }
    .section:nth-child(2) .block__title._active .block__historie .block__text {
        padding-left: 0px;
        padding-top: 10px;
    }
    .section:nth-child(2) .block__title._active .block__historie .block__text p {
        width: 80%;
    }
    .section:nth-child(2) .block__left .block__year {
        display: none;
    }
    .section:nth-child(2) .block__title._active span {
        font-size: calc(10px + (4 + 4 * 0.7) * (100vw - 320px) / 800);;
        padding-bottom: 20px;
        line-height: 16px;
    }
    .footer {
        margin-top: 40px;
    }
    .footer {
        width: 100%;
        height: 479px;
        background: #191919;
        padding: 40px 0px;
    }
    .section:nth-child(2) h2 {
        text-align: center;
        padding-bottom: 0px;
    }

    .section:nth-child(2) .block__title._active .block__historie .phone {
        width: 50%;
    }
    .section:nth-child(2) .block__title._active .block__historie {
        align-items: start;
    }
    .section:nth-child(2) .block__title._active .block__historie .block__text h3 {
        font-size: calc(18px + (6 + 6 * 0.7) * (100vw - 320px) / 800);;
        line-height: 26px;
    }
    .section:nth-child(2) .block__title._active .block__historie .block__text p {
        font-size: calc(10px + (6 + 6 * 0.7) * (100vw - 320px) / 800);;
        line-height: 14px;
    }
    .section:nth-child(2) .block__title._active .block__historie {
        flex-direction: column;
    }
}
@media screen and(max-width: 400px) {
    .section:nth-child(1) .container .block__h1 p {
        padding: 10px 0px 25px 0px;
        width: 70%;
        font-size: 14px;
        font-weight: 400;
        margin: 0 auto;
        span {
            display: none;
        }
    }
}